body {
  width: 350px;
  background: linear-gradient(135deg, #f6d365 0%, #fda085 100%);
  font-family: "Arial", sans-serif;
  margin: 0;
  padding: 15px;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.joke-container {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  min-height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.joke-text {
  font-size: 16px;
  color: #333;
  margin-bottom: 15px;
  line-height: 1.5;
}

.button-group {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.btn {
  padding: 10px 15px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  transition: transform 0.2s, opacity 0.2s;
}

.btn:hover {
  transform: scale(1.05);
}

.btn:active {
  transform: scale(0.95);
}

.btn-reload {
  background-color: #4a90e2;
  color: white;
}

.btn-speak {
  background-color: #2ecc71;
  color: white;
}

.btn-speak:disabled {
  background-color: #95a5a6;
  cursor: not-allowed;
  opacity: 0.7;
}

.loading {
  color: #666;
  font-style: italic;
}
